@font-face {
  font-family: 'Thirsty';
  src: url('../thirsty-font/_thirstyrough-webfont.woff2') format('woff2'),
}

$lightblue:#00a6cf;
$white:#fff;
$orange:#e78601;

@mixin margin{
  margin:0;
  padding:0;
}

body{
  @include margin;
  font-size: 14px;
  // overflow-x: hidden;
  font-family: 'Raleway', sans-serif;
  width:100%;
  height:auto;
}

.spacer{
  width:100%;
  height:75vh;
}

.aboutDesktopOuter{
  width:100%;
  height:auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.leadership{
  width:90%;
  height:auto;
  display:grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  place-content:center;
  gap:20px;
  grid-template-areas: ". . .";

  @media screen and (max-width:900px){
    display: none;
  }
}

.leadershipItem{
  width:100%;
  height:auto;
  display: flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;

  a{
    width:100%;
    height:auto;
    text-decoration:none;
    color:#000;
  }

  img{
    width:100%;
    height:auto;
  }
  p{
    text-align: center;
  }
}

.employees{
  width:90%;
  height:auto;
  display:grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows:auto;
  grid-template-areas: ". . . . .";
  gap:20px;

  @media screen and (max-width:900px){
    display: none;
  }
}

.employeeItem{
  width:100%;
  height:auto;
  display: flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;

  img{
    width:100%;
    height:auto;
  }
  p{
    text-align: center;
  }
}

.aboutMobileOuter{
  display: none;
  @media screen and (max-width:899px){
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    grid-template-areas:". .";
  }
}

.aboutmobileitem{
  text-align: center;
}
